<template>
	<gui-page :fullPage="true" :isLoading="pageLoading" 
	ref="guiPage" :customFooter="true">
		<view slot="gBody" class="gui-flex1 gui-flex gui-columns gui-padding">
			<!-- 顶部步骤提示 -->
			
			
				<view class="">
					<text class="gui-h6 gui-color-gray gui-bold">考官</text>
				</view>
				<view class="gui-form-item gui-border-b">
					<text class="gui-form-label"><text class="gui-color-red">*</text>考官姓名</text>
					<view class="gui-form-body">
						<input type="text" class="gui-form-input" 
						v-model="formData.name1" name="principal-name" placeholder="请输入考官姓名" />
					</view>
				</view>
				<view class="gui-form-item gui-border-b">
					<text class="gui-form-label"><text class="gui-color-red">*</text>考官电话</text>
					<view class="gui-form-body">
						<input type="text" class="gui-form-input" 
						v-model="formData.tel1" name="principal-tel" placeholder="请输入考官电话(手机号)" />
					</view>
				</view>
				<view class="gui-form-item gui-border-b">
					<text class="gui-form-label"><text class="gui-color-red">*</text>身份证号</text>
					<view class="gui-form-body">
						<input type="text" class="gui-form-input" 
						v-model="formData.id1" name="id" placeholder="请输入考官身份证号码" />
					</view>
				</view>
				<view class="gui-margin-top-large">
					<text class="gui-text gui-color-gray">身份证照片 ( 个人信息面 )</text>
				</view>
				<view class="gui-idcard-items gui-img-in gui-flex gui-rows gui-justify-content-center">
					<view class="gui-idcard-items-image" @tap="selectImg1">
						<gui-image :src="formData.idCard1" :width="380"></gui-image>
					</view>
				</view>
				<view class="gui-margin-top-large">
					<text class="gui-text gui-color-gray">身份证照片 ( 国徽图案面 )</text>
				</view>
				<view class="gui-idcard-items gui-img-in gui-flex gui-rows gui-justify-content-center">
					<view class="gui-idcard-items-image" @tap="selectImg2">
						<gui-image :src="formData.idCard2" :width="380"></gui-image>
					</view>
				</view>
				
				
				<view class="gui-form-item gui-border-b gui-margin-top">
					<text class="gui-form-label"><text class="gui-color-red">*</text>考官级别</text>
					<view class="gui-form-body">
					<picker mode="selector" :range="formData.gender" @change="pickerChange">
							<view class="gui-flex gui-rows gui-nowrap gui-space-between gui-align-items-center">
								<text class="gui-text">{{formData.gender[genderIndex]}}</text>
								<text class="gui-form-icon gui-icons gui-text-center gui-color-gray">&#xe603;</text>
							</view>
					</picker>
					</view>
				</view>
				
				<view class="gui-margin-top-large">
					<text class="gui-text gui-color-gray">考官照片 ( 正面 )</text>
				</view>
				<view class="gui-idcard-items gui-img-in gui-flex gui-rows gui-justify-content-center">
					<view class="gui-idcard-items-image" @tap="selectImg3">
						<gui-image :src="formData.idCard3" :width="380"></gui-image>
					</view>
				</view>
				<view class="gui-margin-top-large">
					<text class="gui-text gui-color-gray">考官照片 ( 背面 )</text>
				</view>
				<view class="gui-idcard-items gui-img-in gui-flex gui-rows gui-justify-content-center">
					<view class="gui-idcard-items-image" @tap="selectImg4">
						<gui-image :src="formData.idCard4" :width="380"></gui-image>
					</view>
				</view>
				
				
				</view>
			
		<!-- 底部按钮 -->
		<view slot="gFooter">
			<view class="form-add-footer gui-border-b gui-flex gui-rows gui-space-between" 
			v-if="step == 1" hover-class="gui-tap">
				<text class="form-add-footer-btns"></text>
				<text class="form-add-footer-btns" @tap="submit()">提交</text>
			</view>
			
		</view>
	</gui-page>
</template>
<script>
var graceJS = require('@/GraceUI5/js/grace.js');
export default {
	data() {
		return {
			// 全屏 loading
			pageLoading: true,
			// 核心区域高度 
			mainHeight: 200,
			// 步骤
			step: 1,
			// picker
			genderIndex : 0,
			// 同上按钮
			checkVal:'0',
			checked:false,
			// 表单数据记录
			formData: {
				name1:'',//校长姓名
				tel1:'',//校长电话
				id1:'',//身份证号
				
				gender : ['请选择考官级别', '国家级考官', '高级考官', '考官'],//时间
		idCard1 : 'https://img-cdn-tc.dcloud.net.cn/uploads/article/20210107/e53bf48607dcf795ab78e419dc5cf432.png',
			idCard2 : 'https://img-cdn-tc.dcloud.net.cn/uploads/article/20210107/dac9c5c703b2a0bcd254a919d0da5632.png',
			idCard3 : 'https://img-cdn-tc.dcloud.net.cn/uploads/article/20210107/e53bf48607dcf795ab78e419dc5cf432.png',
			idCard4 : 'https://img-cdn-tc.dcloud.net.cn/uploads/article/20210107/dac9c5c703b2a0bcd254a919d0da5632.png',
			region: ['请选择省份','请选择城市','请选择区域'],//地址省市
			},
			
			customItem: '全部',//默认项
		}
	},
	onLoad:function(){
		// 01. 获取页面主体高度
		graceJS.getRefs('guiPage', this, 0, (ref)=>{
			ref.getDomSize('guiPageBody', (e)=>{
				// 02. 导航的高度
				// 可以 使用 graceJS.select() 函数动态获取， 也可以使用 uni.upx2px 转换已知高度
				// 轮播主体高度 = 页面高度 - 导航高度
				this.mainHeight  = e.height - uni.upx2px(160);
				this.pageLoading = false;
			});
		});
	},
	methods: {
		bindRegionChange:function(e){
		  // console.log((e.detail.value))
		    this.region=e.detail.value
		},
		 
		
		
		  // idcard
		  selectImg1 : function() {
		  	uni.chooseImage({
		  		count:1,
		  		success:(res) => {
		  			this.formData.idCard1 = res.tempFilePaths[0];
		  		}
		  	})
		  },
		  selectImg2 : function() {
		  	uni.chooseImage({
		  		count:1,
		  		success:(res)=>{
		  			this.formData.idCard2 = res.tempFilePaths[0];
		  		}
		  	})
		  },
		  selectImg3 : function() {
		  	uni.chooseImage({
		  		count:1,
		  		success:(res) => {
		  			this.formData.idCard3 = res.tempFilePaths[0];
		  		}
		  	})
		  },
		  selectImg4: function() {
		  	uni.chooseImage({
		  		count:1,
		  		success:(res)=>{
		  			this.formData.idCard4 = res.tempFilePaths[0];
		  		}
		  	})
		  },
		  uploadCards : function(){
		  	if(
		  	this.formData.idCard1 == 'https://img-cdn-tc.dcloud.net.cn/uploads/article/20210107/e53bf48607dcf795ab78e419dc5cf432.png' || 
		  	this.formData.idCard2 == 'https://img-cdn-tc.dcloud.net.cn/uploads/article/20210107/dac9c5c703b2a0bcd254a919d0da5632.png'||
		  	this.formData.idCard3 == 'https://img-cdn-tc.dcloud.net.cn/uploads/article/20210107/e53bf48607dcf795ab78e419dc5cf432.png' ||
		  	this.formData.idCard4 == 'https://img-cdn-tc.dcloud.net.cn/uploads/article/20210107/dac9c5c703b2a0bcd254a919d0da5632.png'
		  	
			){
		  		uni.showToast({title:"请确认是否上传身份证照片和考官照片", icon:"none"});
		  		return;
		  	}
		  	// 临时文件地址保存在 this.idCard1 和 this.idCard2 
		  	// 请调用  uni.uploadFile api 完成上传工作
		  },
		// picker 切换
		pickerChange : function (e) {
			console.log(e);
			this.genderIndex    = e.detail.value;
			this.formData.name4 = this.gender[this.genderIndex];
		},
		// switch 开关
		switchChange : function (e) {
			this.formData.name6 = e.detail.value;
		},
		// stepto : function(index){
		// 	console.log(this.formData);
		// 	switch(index){
		// 		case 1:
		// 		// 第1步数据验证这里编写
		// 		// 不满足使用 return 返回即可
		// 		break;
		// 		case 2:
		// 		// 第2步数据验证这里编写
		// 		// 不满足使用 return 返回即可
		// 		break;
		// 		case 3:
		// 		// 第3步数据验证这里编写
		// 		// 不满足使用 return 返回即可
		// 		break;
		// 	}
		// 	this.step = index;
		// },
		// 提交
		submit:function () {
			// 数据保持在 this.form data 内
			// 请根据实际需求进行数据校验及提交
			this.uploadCards()
			if(!this.formData.name1||!this.formData.tel1||!this.formData.id1){
				uni.showToast({title:"请确认是否填写完全", icon:"none"});
			}else{
				// console.log(this.formData);
			uni.redirectTo({
			    url: '../enroll/enroll'
			});
			}
			
		}
	}
}
</script>
<style>
.gui-step-item{padding:10rpx 20rpx; width:88rpx; display:flex; flex-direction:column; align-items:center; flex-shrink:0;}
.gui-step-icon{width:50rpx; font-size:22rpx; height:50rpx; text-align:center; line-height:50rpx; margin:10rpx; border-radius:100%; background-color:#F1F2F3; color:#999999;}
.gui-step-text{text-align:center; line-height:50rpx; font-size:24rpx; color: gray;}
.gui-step-line{width:100rpx; height:1px; background-color:#F1F2F3; margin-bottom:50rpx; flex:1;}
.gui-step-line-active{background-color:#ffcb52;}
.form-add-footer-btns{padding:0 38rpx; color:#ffcb52; font-size:30rpx; line-height:100rpx;}
</style>